* {
   box-sizing:border-box;
}
.container, .qrcode {
   display:grid;
   grid-template-rows:1fr 1fr 1fr;
   margin:0 auto;
   max-width:760px;
   min-width:450px;
   height:50vh;
}
.qrcode {
   display:flex;
   align-items:center;
   justify-content:center;
   margin-top:50px;
   min-height:15vh;
   max-height:35vh;
   border:1px solid lightgray;
   background:transparent;
}
.hint::before {
   background-image:url('hintbot.png');
   display:inline-block;
   margin-left:5px;
   vertical-align:-5px;
   background-size:50px 62px;
   width:50px;
   height:62px;
   content:'';
}
.hintinput {
   margin-left:20px;
   margin-top:20px;
   width:calc(100% - 100px);
   height:75%;
   outline:none;
   border:1px solid lightgray;
   padding:5px;
   border-radius:2px;
   font-family:sans-serif;
   font-size:100%;
   resize:none;
}
.generate {
   margin:20px auto;
   height:45px;
   width:50%;
   background-color:white;
   outline:none;
   font-variant:small-caps;
   border:none;
   box-shadow:0 0 5px #ccc;
   font-family:sans-serif;
   font-size:100%;
   cursor:pointer;
   border-radius:5px;
}
.tooltip {
  position:relative;
  display:flex;
  align-items:center;
  justify-content:center;
  margin:0 auto;
  color:white;
  font-family:sans-serif;
  width:30px;
  height:30px;
  border-radius:50%;
  background-color:#555;
}
.tooltip .tooltiptext {
  visibility:hidden;
  width:120px;
  background-color:#555;
  color:#fff;
  text-align:center;
  box-sizing:border-box;
  border-radius:6px;
  padding:8px;
  position:absolute;
  z-index:1;
  bottom:125%;
  left:50%;
  margin-left:-60px;
  opacity:0;
  transition:opacity 0.3s;
}
.tooltip:hover .tooltiptext {
  visibility:visible;
  opacity:1;
}
.tooltiptext {
  font-size:80%;
}
